function renderYesNo(bool) {
    return bool ?
        <span className="boolText" style={{ color: 'green' }}>Yes</span> :
        <span className="boolText" style={{ color: 'red' }}>No</span>;
}

class BoardTimer extends React.Component {
    constructor() {
        super();
        globalBoardTimer = this;
        this.state = {
            previouslyAccumulatedSeconds: 0.0,
            timerStartMS: 0.0,
            timerRunning: false,
            includesLoadingTheRoom: true,
            includedRewardsGotten: 0,
            invalidated: false,
        };
    }

    toggleRunning() {
        const now = performance.now();
        const elapsed = 1e-3 * (now - this.state.timerStartMS);
        if (this.state.timerRunning)
            this.setState({previouslyAccumulatedSeconds: this.state.previouslyAccumulatedSeconds + elapsed});
        this.setState({timerRunning: !this.state.timerRunning, timerStartMS: now});
    }

    adjustRewards(delta) {
        this.setState({includedRewardsGotten: Math.max(0, Math.min(2, this.state.includedRewardsGotten + delta))});
    }

    toggleInvalidated() {
        this.setState({invalidated: !this.state.invalidated});
    }

    resetTimer() {
        this.setState({
            previouslyAccumulatedSeconds: 0.0,
            timerStartMS: performance.now(),
            timerRunning: false,
            includesLoadingTheRoom: true,
            includedRewardsGotten: 0,
            invalidated: false,
        });
        globalMap.setState({
            timerStepEstimates: [],
        });
    }

    getSecondsElapsed() {
        let total = this.state.previouslyAccumulatedSeconds;
        if (this.state.timerRunning) {
            const now = performance.now();
            total += 1e-3 * (now - this.state.timerStartMS);
        }
        return total;
    }

    guessStepsElapsedFromTime(timeDeltaSeconds) {
        // I did some linear regressions from real HD Italian runs. I'll put some data up at some point.
        let prediction = Number(this.props.timedTickIntercept) + Number(this.props.timedTickRate) * timeDeltaSeconds;
        if (this.state.includesLoadingTheRoom)
            prediction += -940 + Number(this.props.roomEnteredOffset);
        prediction += this.state.includedRewardsGotten * 760;
        return Math.round(prediction);
    }

    render() {
        const elapsed = this.getSecondsElapsed();
        if (this.state.invalidated)
            return <tr>
                <td><b>Timer</b></td>
                <td><b>Invalidated</b></td>
            </tr>;
        return <>
            <tr>
                <td>Seconds elapsed:</td>
                <td>{elapsed.toFixed(2)}</td>
            </tr>
            <tr>
                <td>Steps:</td>
                <td>{this.guessStepsElapsedFromTime(elapsed)}</td>
            </tr>
            <tr>
                <td>Entered room:</td>
                <td>{renderYesNo(this.state.includesLoadingTheRoom)}</td>
            </tr>
            <tr>
                <td>Rewards gotten:</td>
                <td>{this.state.includedRewardsGotten}</td>
            </tr>
        </>;
    }
}

function computeL1Distance(p1, p2) {
    return Math.abs(p1[0] - p2[0]) + Math.abs(p1[1] - p2[1]);
}

const defaultConfigurationParams = {
    firstBoardStepsThousands: 100,
    firstBoardStepsThousandsStdDev: 150,
    nextBoardStepsThousands: 9,
    nextBoardStepsThousandsStdDev: 4,
    timedBoardStepsThousandsStdDev: 0.5,
    timedTickIntercept: 550,
    timedTickRate: 243.3,
    roomEnteredOffset: -550,
    usingTimer: true,
    autoRecomputeMatches: false,
    showAdvancedSettings: false,
};

class MainMap extends React.Component {
    layoutDrawingBoardRefs = [React.createRef(), React.createRef()];
    timerRef = React.createRef();

    constructor() {
        super();
        this.state = this.makeEmptyState();
        globalMap = this;
    }

    componentDidMount() {
        // As unlikely as it may be, indexedDB could be ready by now.
        // Therefore, check it in addition to marking it ready.
        this.setState({ doneInitializing: true }, setLargestLocalTableSize);
        this.doComputation(this.state.grid, this.state.squidsGotten);
    }

    makeEmptyGrid() {
        const grid = [];
        for (let y = 0; y < 8; y++)
            for (let x = 0; x < 8; x++)
                grid[[x, y]] = null;
        return grid;
    }

    makeEmptyState() {
        const probs = [];
        for (let y = 0; y < 8; y++)
            for (let x = 0; x < 8; x++)
                probs[[x, y]] = 0.0;
        const state = {
            grid: this.makeEmptyGrid(),
            squidsGotten: 'unknown',
            undoBuffer: [],
            probs,
            best: [3, 4],
            cursorBelief: [0, 7],
            valid: true,
            observationProb: 1.0,
            lastComputationTime: -1,

            sequenceAware: false,
            tableSize: '500k',
            largestLocalTableSize: null,
            usingTimer: false,
            showKeyShortcuts: false,

            timerStepEstimates: [],

            potentialMatches: null,
        };
        // Load relevant configuration from localStorage.
        let savedSettings = localStorage.getItem('SKSettings');
        if (savedSettings === null) {
            savedSettings = defaultConfigurationParams;
        } else {
            // if saved configuration from previous version, use defaults for 
            // any new parameters
            savedSettings = JSON.parse(savedSettings);
            for (const name of Object.keys(defaultConfigurationParams)) {
                if (!(name in savedSettings)){
                    savedSettings[name] = defaultConfigurationParams[name];
                }
            }
        }
        const configParams = savedSettings;
        return {...state, ...configParams};
    }

    getConfigParams() {
        const settings = {};
        for (const name of Object.keys(defaultConfigurationParams))
            settings[name] = this.state[name];
        return settings;
    }

    saveConfigParams() {
        const configParams = this.getConfigParams();
        console.log('Saving config params:', configParams);
        localStorage.setItem('SKSettings', JSON.stringify(configParams));
    }

    factoryResetConfigParams() {
        this.setState(defaultConfigurationParams);
    }

    async loadSequenceTable() {
        if (this.state.sequenceAware !== false)
            return;
        this.setState({sequenceAware: 'initializing'});
        this.boardIndices = await makeBoardIndicesTable();
        this.boardIndexToLayoutString = new Array(Object.keys(this.boardIndices).length);
        for (const key of Object.keys(this.boardIndices))
            this.boardIndexToLayoutString[this.boardIndices[key]] = key;

        const tableName =  '/board_table_' + this.state.tableSize + '.bin';
        dbCachedFetch(tableName, (buf) => {
            // Store the table size in indexedDB so we easily know we have it.
            dbWrite('largestLocalTableSize', this.state.tableSize);
            this.boardTable = new Uint32Array(buf);
            // Warning: Do I need to await wasm here first?
            console.log('Board table length:', this.boardTable.length);
            set_board_table(this.boardTable);
            this.setState({ sequenceAware: true }, () => {
                this.clearField();
            });
        });
    }

    *findMatchingLocations(observedBoards, startIndex, scanRange) {
        // Try to find matches for the next board.
        const soughtBoard = observedBoards[0];
        const remainingBoards = observedBoards.slice(1);
        const boardTable = this.boardTable;
        const indexMax = Math.min(boardTable.length, startIndex + scanRange);
        for (let i = startIndex; i < indexMax; i++)
            if (boardTable[i] === soughtBoard)
                if (remainingBoards.length > 0)
                    for (const subResult of this.findMatchingLocations(remainingBoards, i, 50000))
                        yield [i, ...subResult];
                else
                    yield [i];
    }

    recomputePotentialMatches(all_matches) {
        let scanStart = 0;
        let scanRange = 1000000000;
        if (!all_matches) {
            const nearlyCertainRadius = 5000 * this.state.firstBoardStepsThousandsStdDev;
            scanStart = 1000 * this.state.firstBoardStepsThousands - nearlyCertainRadius;
            scanRange = 2 * nearlyCertainRadius;
            // Due to how indexing arrays works, this check technically isn't
            // necessary. However, it is a simple optimization for speed.
            if (scanStart < 0) {
                scanRange += scanStart;
                scanStart = 0;
            }
        }
        const observedBoards = this.makeGameHistoryArguments()[0];
        const matches = [];
        if (observedBoards.length > 0)
            for (const match of this.findMatchingLocations(observedBoards, scanStart, scanRange))
                matches.push(match);
        this.setState({potentialMatches: matches});
    }

    makeGameHistoryArguments() {
        // Figure out how many history boards we have.
        const rawObservedBoards = this.layoutDrawingBoardRefs
            .map((ref) => this.boardIndices?.[ref.current?.getLayoutString()]);
        const observedBoards = [];
        for (const ob of rawObservedBoards) {
            if (ob === undefined)
                break;
            observedBoards.push(ob);
        }

        // The optimal thing to do here is to save the sequence of step delta estimates, but to make
        // the tool less fragile we only use our timer-based estimates for the very final mean.

        const means = [1000.0 * Number(this.state.firstBoardStepsThousands)];
        const stds = [1000.0 * Number(this.state.firstBoardStepsThousandsStdDev)];
        for (let i = 0; i < observedBoards.length; i++) {
            means.push(1000.0 * Number(this.state.nextBoardStepsThousands));
            stds.push(1000.0 * Number(this.state.nextBoardStepsThousandsStdDev));
        }
        const estimates = this.state.timerStepEstimates;
        const lastEstimate = estimates[estimates.length - 1];
        if (observedBoards.length > 0 && estimates.length > 0 && lastEstimate !== null) {
            means[means.length - 1] = Math.max(0, lastEstimate);
            const timedStd = 1000.0 * Number(this.state.timedBoardStepsThousandsStdDev);
            // There is more uncertainty in the second board than ones after.
            stds[stds.length - 1] = observedBoards.length === 1 ? 2.0 * timedStd
                : 0.5 * timedStd;
        }

        return [
            Uint32Array.from(observedBoards),
            Uint32Array.from(means),
            Float64Array.from(stds),
        ];
    }

    getGridStatistics(grid, squidsGotten) {
        const hits = [];
        const misses = [];
        for (let y = 0; y < 8; y++) {
            for (let x = 0; x < 8; x++) {
                const gridValue = grid[[x, y]];
                if (gridValue === 'HIT')
                    hits.push(8 * y + x);
                if (gridValue === 'MISS')
                    misses.push(8 * y + x);
            }
        }
        let numericSquidsGotten = -1;
        for (const n of ['0', '1', '2', '3'])
            if (squidsGotten === n || squidsGotten === Number(n))
                numericSquidsGotten = Number(n);
        return {hits, misses, numericSquidsGotten};
    }

    async doComputation(grid, squidsGotten) {
        console.log('Doing computation:', squidsGotten, grid);
        const t0 = performance.now();
        const {hits, misses, numericSquidsGotten} = this.getGridStatistics(grid, squidsGotten);

        await wasm;
        let probabilities;
        let gameHistoryArguments = null;
        if (this.state.sequenceAware) {
            gameHistoryArguments = this.makeGameHistoryArguments();
            console.log('gameHistoryArguments:', gameHistoryArguments);

            probabilities = calculate_probabilities_from_game_history(
                Uint8Array.from(hits),
                Uint8Array.from(misses),
                numericSquidsGotten,
                ...gameHistoryArguments,
            );
        } else {
            probabilities = calculate_probabilities_without_sequence(
                Uint8Array.from(hits),
                Uint8Array.from(misses),
                numericSquidsGotten,
            );
        }

        if (probabilities !== undefined) {
            let maxY = 0;
            let maxX = 0;
            let highestProb = -1;
            let probs = [];

            // Here we implement our L1 distance bonus heuristic.
            // The idea is that we want to highlight a square that isn't too far from where
            // the player last adjusted the board. (i.e. where we believe their cursor is.)
            for (let y = 0; y < 8; y++) {
                for (let x = 0; x < 8; x++) {
                    probs[[x, y]] = probabilities[8 * y + x];
                    const l1Distance = computeL1Distance(this.state.cursorBelief, [x, y]);
                    const distancePenaltyMultiplier = 1 - 0.04 * l1Distance;
                    const distanceAdjustedProb = probabilities[8 * y + x] * distancePenaltyMultiplier;
                    if (grid[[x, y]] === null && distanceAdjustedProb > highestProb) {
                        highestProb = distanceAdjustedProb;
                        maxX = x;
                        maxY = y;
                    }
                }
            }
            const observationProb = probabilities[64];
            this.setState({ probs, best: highestProb > 0 ? [maxX, maxY] : null,
                valid: true, observationProb });
        } else {
            this.setState({ valid: false, best: null });
        }
        const t1 = performance.now();
        this.setState({lastComputationTime: t1 - t0});
    }

    copyToUndoBuffer() {
        this.setState({undoBuffer: [
            ...this.state.undoBuffer,
            {grid: this.state.grid, squidsGotten: this.state.squidsGotten, cursorBelief: this.state.cursorBelief},
        ]});
    }

    onClick(x, y, setAsHit) {
        const grid = { ...this.state.grid };
        this.copyToUndoBuffer();

        switch (grid[[x, y]]) {
            case 'MISS':
                grid[[x, y]] = 'HIT';
                break;
            case 'HIT':
                grid[[x, y]] = null;
                break;
            default:
                grid[[x, y]] = setAsHit ? 'HIT' : 'MISS';
                break;
        }
        this.setState({grid, cursorBelief: [x, y]});
        this.doComputation(grid, this.state.squidsGotten);
    }

    clearField() {
        const templateState = this.makeEmptyState();
        const newState = {};
        for (const name of ['grid', 'squidsGotten', 'undoBuffer', 'cursorBelief'])
            newState[name] = templateState[name];
        // The squidsGotten value of 'unknown' is banned in sequence-aware mode.
        if (this.state.sequenceAware)
            newState.squidsGotten = '0';
        this.setState(newState);
        this.doComputation(newState.grid, newState.squidsGotten);
    }

    undoLastMarking() {
        const undoBuffer = [...this.state.undoBuffer];
        if (undoBuffer.length === 0)
            return;
        const undoEntry = undoBuffer.pop();
        this.setState({grid: undoEntry.grid, squidsGotten: undoEntry.squidsGotten, cursorBelief: undoEntry.cursorBelief, undoBuffer});
        this.doComputation(undoEntry.grid, undoEntry.squidsGotten);
    }

    reportMiss() {
        if (this.state.best !== null && this.state.grid[this.state.best] === null) {
            this.onClick(...this.state.best);
        }
    }

    reportHit() {
        if (this.state.best !== null && this.state.grid[this.state.best] === null) {
            this.onClick(...this.state.best, true);
            const {hits} = this.getGridStatistics(this.state.grid, this.state.squidsGotten);
            if (hits.length === 9 && this.state.squidsGotten !== 'unknown') {
                this.incrementKills();
            }
        }
    }

    splitTimer() {
        const boardTimer = this.timerRef.current;
        let newEstimates = [...this.state.timerStepEstimates];
        if (boardTimer !== null) {
            const elapsed = boardTimer.getSecondsElapsed();
            if (!elapsed && !boardTimer.state.invalidated) {
                boardTimer.toggleRunning();
                return;
            }
            const timerStartMS = performance.now();
            newEstimates.push(boardTimer.state.invalidated ? null
            : boardTimer.guessStepsElapsedFromTime(elapsed));

            console.log('Timer step estimates:', newEstimates);
            boardTimer.setState({
                previouslyAccumulatedSeconds: 0.0,
                timerStartMS,
                // After the first split we're no longer loading the room.
                includesLoadingTheRoom: false,
                includedRewardsGotten: 0,
                timerRunning: true,
                invalidated: false,
            });
        }
        // Automatically copy board to history if it is unambiguous.
        if (this.copyToHistory()) {
            const squidsGotten = '0';
            const grid = this.makeEmptyGrid();
            // TODO: Allow undoing across completions.
            this.setState({
                    timerStepEstimates: newEstimates,
                    undoBuffer: [],
                    cursorBelief: [0, 7],
                    grid,
                    squidsGotten,
                }, () => {
                    // The copy to history should be done by now, even if it
                    // got batched together with this setState.
                    this.doComputation(grid, squidsGotten);
                    if (this.state.autoRecomputeMatches)
                        this.recomputePotentialMatches(false);
            });
        } else {
            // There are still things to update, even if not finishing a board.
            this.setState({timerStepEstimates: newEstimates}, () => {
                this.doComputation(this.state.grid, this.state.squidsGotten);
                // The tradeoff here is that we can find the matches after the
                // thrown board, but they are also found when the prediction
                // board is ambiguous.
                if (this.state.usingTimer && this.state.autoRecomputeMatches)
                        this.recomputePotentialMatches(false);

            });
        }
    }

    incrementKills() {
        let numericValue = this.state.squidsGotten === 'unknown' ? 0 : Number(this.state.squidsGotten);
        if (!this.state.sequenceAware && numericValue === 3)
            return;
        this.copyToUndoBuffer();
        let grid = this.state.grid;
        if (numericValue < 3)
            numericValue++;
        this.setState({grid, squidsGotten: '' + numericValue});
        this.doComputation(grid, '' + numericValue);
    }

    copyToHistory(gameHistoryArguments) {
        if (!this.state.sequenceAware)
            return;
        const {hits, misses, numericSquidsGotten} = this.getGridStatistics(this.state.grid, this.state.squidsGotten);
        if (gameHistoryArguments === undefined)
            gameHistoryArguments = this.makeGameHistoryArguments();
        const finalBoard = disambiguate_board(
            Uint8Array.from(hits),
            Uint8Array.from(misses),
            numericSquidsGotten,
            ...gameHistoryArguments,
        );
        if (finalBoard === undefined) {
            // TODO: Show a proper error message in this case!
            return false;
        }
        console.log('Final board:', finalBoard);
        const layoutString = this.boardIndexToLayoutString[finalBoard];
        const observedBoards = gameHistoryArguments[0];
        let fillIndex = observedBoards.length;
        // If we're already at capacity then we have to shift the boards over.
        if (fillIndex === this.layoutDrawingBoardRefs.length) {
            this.shiftHistory();
            fillIndex--;
        }
        this.layoutDrawingBoardRefs[fillIndex].current.setStateFromLayoutString(layoutString);
        return true;
    }

    shiftHistory() {
        const drawingBoards = this.layoutDrawingBoardRefs.map((ref) => ref.current);
        for (let i = 0; i < drawingBoards.length -1; i++) {
            drawingBoards[i].setState(drawingBoards[i + 1].state);
        }
        drawingBoards[drawingBoards.length - 1].clearBoard();
    }

    clearHistory() {
        for (const ref of this.layoutDrawingBoardRefs) {
            ref.current.clearBoard();
        }
    }

    renderActualMap() {
        return <div className="board">
            {naturalsUpTo(8).map(
                (y) =>
                    naturalsUpTo(8).map(
                        (x) => <Tile
                            key={x + ',' + y}
                            x={x} y={y}
                            onClick={() => this.onClick(x, y)}
                            text={this.state.grid[[x, y]]}
                            prob={this.state.probs[[x, y]]}
                            valid={this.state.valid}
                            best={this.state.best}
                            precision={2}
                        />
                    )
            )}
        </div>;
    }

    render() {
        let usedShots = 0;
        let openingOptimizer = true;
        for (let y = 0; y < 8; y++) {
            for (let x = 0; x < 8; x++) {
                if (this.state.grid[[x, y]] !== null) {
                    usedShots++;
                    if (this.state.grid[[x, y]] === 'HIT')
                        openingOptimizer = false;
                }
            }
        }

        let timerStepEstimateText = !this.state.timerStepEstimates.length ? '-'
            : this.state.timerStepEstimates.map((v) => {
                return v ?? '(' + this.state.nextBoardStepsThousands * 1000 + ')';
            }).join(', ');

        const largestLocalTable = Number(this.state.largestLocalTableSize
            ?.replace(/k/i, '000')?.replace(/M/i, '000000') ?? 0);
        const wrongTableWarning = "You have a larger table available locally. " +
                                  "Are you sure you want to switch tables?";

        return <>
            <div className="container">
                <div style={{ placeSelf: "start end" }}>
                    <div id="infoArea">
                        <table>
                            <thead><tr><th>Item</th><th>Value</th></tr></thead>
                            <tbody>
                                <tr><td>Shots used:</td><td>{usedShots}</td></tr>
                                {this.state.sequenceAware === true && this.state.usingTimer && <>
                                    <BoardTimer ref={this.timerRef} roomEnteredOffset={this.state.roomEnteredOffset} timedTickIntercept={this.state.timedTickIntercept} timedTickRate={this.state.timedTickRate}/>
                                    <tr>
                                        <td>Previous steps:</td>
                                        <td>{timerStepEstimateText}</td>
                                    </tr>
                                </>}
                            </tbody>
                        </table>
                        {this.state.sequenceAware === true && this.state.usingTimer && this.state.showKeyShortcuts && <table>
                            <thead><tr><th>Control</th><th>Shortcut</th></tr></thead>
                            <tbody>
                                <tr><td>Add Reward</td><td>,</td></tr>
                                <tr><td>Remove Reward</td><td>Shift+,</td></tr>
                                <tr><td>Invalidate Timer</td><td>;</td></tr>
                                <tr><td>Reset Timer</td><td>Shift+;</td></tr>
                                <tr><td>Pause/Resume Timer</td><td>w</td></tr>
                                <tr><td>Undo</td><td>Ctrl+z or Shift+z</td></tr>
                            </tbody>
                        </table>}
                    </div>
                    {this.state.sequenceAware === true &&
                        this.state.usingTimer &&
                        <div className="controls">
                            <button onClick={() => {
                                this.setState({
                                    showKeyShortcuts: !this.state.showKeyShortcuts
                            })}}>
                                Toggle Show Shortcuts
                            </button>
                        </div>}
                </div>
                {this.renderActualMap()}
            </div>
            {!this.state.valid && !this.state.sequenceAware &&
                <div>Invalid configuration! This is not possible.</div>}
            <div className="controls" style={{ marginTop: '5px' }}>
                Number of squids killed:
                <select
                    value={this.state.squidsGotten}
                    onChange={(event) => {
                        this.setState({ squidsGotten: event.target.value });
                        this.doComputation(this.state.grid, event.target.value);
                    }}
                >
                    {/* In sequence-aware mode, don't allow unknown because it's just an accident waiting to happen for a runner. */}
                    {
                        !this.state.sequenceAware &&
                        <option value="unknown">Unknown</option>
                    }
                    <option value="0">0</option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                </select>
            </div>
            <div className="controls">
                <button onClick={() => { this.reportMiss(); }}>
                    Miss (z)
                </button>
                <button onClick={() => { this.reportHit(); }}>
                    Hit (x)
                </button>
                <button onClick={() => { this.incrementKills(); }}>
                    Increment Kills (v)
                </button>
                {
                    this.state.sequenceAware === true &&
                    <>
                        <button onClick={() => { this.splitTimer(); }}>
                            {this.state.usingTimer ? <>Start/Split Timer </>
                                : <>Move to History </>}
                            (Space)
                        </button>
                        <button onClick={() => { this.clearHistory(); }}>
                            Reset History
                        </button>
                    </>
                }
                <button onClick={() => { this.clearField(); }}>
                    Reset Board
                </button>
                {
                    this.state.sequenceAware === true && <button onClick={() => {
                        this.clearHistory();
                        // ESLint gives a false positive if this is done with ?.
                        if (this.timerRef.current !== null) {
                            this.timerRef.current.resetTimer();
                        }
                        this.clearField();
                        this.setState({ potentialMatches: null });
                    }}>
                        Reset Everything
                    </button>
                }
            </div>
            {openingOptimizer && !this.state.sequenceAware && <p>
                Opening optimizer: Probability that this<br />pattern would get at least one hit: {
                    this.state.valid ? toPercent(Math.max(0, 1 - this.state.observationProb), 2)
                        : "Invalid"
                }
            </p>}
            {this.state.sequenceAware === 'initializing' &&
                <p>Loading table...</p>}
            {this.state.sequenceAware === true && <>
                <div>
                    {this.layoutDrawingBoardRefs.map((ref, i) =>
                        <LayoutDrawingBoard parent={this} ref={ref} key={i}/>
                    )}
                </div>
                <hr/>
                <fieldset id="settings"
                    className={
                        this.state.showAdvancedSettings || !this.state.usingTimer ? ""
                        : "simple"
                    }
                >
                    <legend>
                        Gaussian RNG step count beliefs (all counts in <i>
                        thousands</i> of steps{
                            this.state.usingTimer && this.state.showAdvancedSettings &&
                            <>, except "Timed board intercept", "Timed board rate",
                                and "Room entered offset"</>
                        })
                    </legend>
                    <div>
                        <label htmlFor="firstBoardMean">First board mean:</label>
                        <input id="firstBoardMean"
                            value={this.state.firstBoardStepsThousands}
                            onChange={event => this.setState({
                                firstBoardStepsThousands: event.target.value
                        })}/>
                        <label htmlFor="firstBoardStddev">First board stddev:</label>
                        <input id="firstBoardStddev"
                            value={this.state.firstBoardStepsThousandsStdDev}
                            onChange={event => this.setState({
                                firstBoardStepsThousandsStdDev: event.target.value
                        })}/>
                        {(!this.state.usingTimer || this.state.showAdvancedSettings) && <>
                            <label htmlFor="nextBoardMean">
                                {this.state.usingTimer ? <>Fallback mean:</>
                                    : <>Next board mean:</>}
                            </label>
                            <input id="nextBoardMean"
                                value={this.state.nextBoardStepsThousands}
                                onChange={event => this.setState({
                                    nextBoardStepsThousands: event.target.value
                            })}/>
                            <label htmlFor="nextBoardStddev">
                                {this.state.usingTimer ? <>Fallback stddev:</>
                                    : <>Next board stddev:</>}
                            </label>
                            <input id="nextBoardStddev"
                                value={this.state.nextBoardStepsThousandsStdDev}
                                onChange={event => this.setState({
                                    nextBoardStepsThousandsStdDev: event.target.value
                            })}/>
                        </>}
                        {this.state.usingTimer && <>
                            <label htmlFor="timedBoardStddev">Timed board stddev:</label>
                            <input id="timedBoardStddev"
                                value={this.state.timedBoardStepsThousandsStdDev}
                                onChange={event => this.setState({
                                    timedBoardStepsThousandsStdDev: event.target.value
                            })}/>
                            {this.state.showAdvancedSettings && <>
                                <label htmlFor="timedBoardIntercept">Timed board intercept:</label>
                                <input id="timedBoardIntercept"
                                    value={this.state.timedTickIntercept}
                                    onChange={event => this.setState({
                                        timedTickIntercept: event.target.value
                                })}/>
                                <label htmlFor="timedBoardRate">Timed board rate:</label>
                                <input id="timedBoardRate"
                                    value={this.state.timedTickRate}
                                    onChange={event => this.setState({
                                        timedTickRate: event.target.value
                                })}/>
                                <label htmlFor="roomEnteredOffset">Room entered offset:</label>
                                <input id="roomEnteredOffset"
                                    value={this.state.roomEnteredOffset}
                                    onChange={event => this.setState({
                                        roomEnteredOffset: event.target.value
                                })}/>
                            </>}
                        </>}
                    </div>
                </fieldset>

                <div className="controls">
                    <label>
                        <input type="checkbox"
                            checked={this.state.usingTimer}
                            onChange={(event) => this.setState({
                                usingTimer: !this.state.usingTimer
                        })} />
                        Use timer
                    </label>
                    <label>
                        <input type="checkbox"
                            checked={this.state.autoRecomputeMatches}
                            onChange={(event) => this.setState({
                                autoRecomputeMatches: !this.state.autoRecomputeMatches
                        })} />
                        Automatically find nearby matches
                    </label>
                    <label>
                        <input type="checkbox"
                            disabled={!this.state.usingTimer}
                            checked={this.state.showAdvancedSettings}
                            onChange={(event) => this.setState({
                                showAdvancedSettings: !this.state.showAdvancedSettings
                        })} />
                        Show advanced settings
                    </label>
                </div>

                <div className="controls">
                    <button onClick={() => { this.saveConfigParams(); }}>
                        Save Settings
                    </button>
                    <button onClick={() => { this.factoryResetConfigParams(); }}>
                        Reset to Defaults
                    </button>
                </div>

                <div style={{
                    margin: '5px',
                    border: '2px solid white',
                    borderRadius: '8px',
                    width: '400px',
                    minHeight: '20px',
                    display: 'inline-block'
                }}>
                    {this.state.potentialMatches?.length === 0 ? <div>No Matches Found!</div>
                        : this.state.potentialMatches?.map((match, i) => {
                            const diffs = match.slice(1);
                            return <div key={i}>
                                Potential match: {match[0]}{diffs.map((x, i) => <> +{x - match[i]}</>)}
                            </div>;
                    })}
                </div>
                <div className="controls">
                    <button onClick={() => {
                        this.recomputePotentialMatches(true);
                    }}>Find Match Indices</button>
                </div>
            </>}
            {!this.state.sequenceAware && <fieldset id="sequenceConfig" >
                <legend>Sequence-Aware Mode</legend>
                <div>
                    <label>
                        <input type="radio" name="tableSize" value="500k"
                            onChange={() => {
                                if (largestLocalTable <= 500000 ||
                                    window.confirm(wrongTableWarning))
                                    this.setState({ tableSize: "500k" });
                            }}
                            checked={ this.state.tableSize === "500k" }
                        />
                        Small table (500 thousand values,
                        {largestLocalTable >= 500000 ? <> local</> : <> 2 MB</>})
                    </label>
                </div>
                <div>
                    <label>
                        <input type="radio" name="tableSize" value="5M"
                            onChange={() => {
                                if (largestLocalTable <= 5000000 ||
                                    window.confirm(wrongTableWarning))
                                    this.setState({ tableSize: "5M" });
                            }}
                            checked={ this.state.tableSize === "5M" }
                        />
                        Medium table (5 million values,
                        {largestLocalTable >= 5000000 ? <> local</> : <> 20 MB</>})
                    </label>
                </div>
                <div>
                    <label>
                        <input type="radio" name="tableSize" value="25M"
                            onChange={() => {
                                if (largestLocalTable <= 25000000 ||
                                    window.confirm(wrongTableWarning))
                                    this.setState({ tableSize: "25M" });
                            }}
                            checked={ this.state.tableSize === "25M" }
                        />
                        Large table (25 million values,
                        {largestLocalTable >= 25000000 ? <> local</> : <> 100 MB</>})
                    </label>
                </div>
                <button onClick={() => { this.loadSequenceTable(); }}>
                    Initialize
                </button>
            </fieldset>}

            <p>
                Last recompute time:&nbsp;
                {this.state.lastComputationTime.toFixed(2)}ms
            </p>
        </>;
    }
}

function globalShortcutsHandler(evt) {
    // OBS doesn't give a code value, but their key value almost works as one.
    const code = evt.code !== '' ? evt.code : evt.key;
    if (globalMap !== null) {
        if (code === 'KeyZ' || code === 'z')
            if (evt.ctrlKey) {
                // Prevent modifying an input when undoing.
                if (evt.target?.tagName !== "INPUT") {
                    evt.preventDefault();
                    globalMap.undoLastMarking();
                }
            }
            else if (evt.shiftKey)
                globalMap.undoLastMarking();
            else
                globalMap.reportMiss();
        if (code === 'KeyX' || code === 'x')
            globalMap.reportHit();
        if (code === 'KeyV' || code === 'v')
            globalMap.incrementKills();
        if (code === 'Space' || code === ' ') {
            globalMap.splitTimer();
            evt.preventDefault();
        }
    }
    if (globalBoardTimer !== null) {
        if (code === 'KeyW' || code === 'w')
            globalBoardTimer.toggleRunning();
        if (code === 'Comma' || code === ',')
            if (!evt.shiftKey)
                globalBoardTimer.adjustRewards(+1);
            else
                globalBoardTimer.adjustRewards(-1);
        if (code === 'Semicolon' || code === ';')
            if (!evt.shiftKey)
                globalBoardTimer.toggleInvalidated();
            else
                globalBoardTimer.resetTimer();
    }
}

document.addEventListener('keydown', globalShortcutsHandler);

class App extends React.Component {
    render() {
        return <>
            <h1>Sploosh Kaboom Probability Calculator</h1>
            <p style={{ marginLeft: 'auto', marginRight: 'auto', maxWidth: '600px' }}>
                This is a tool for computing the likely locations of squids in the sploosh kaboom minigame of The Legend of Zelda: The Wind Waker (both SD and HD versions).
                Tutorials for it should be available Soon.
                For now, see the <a href="https://github.com/Amphitryon0/web-sploosh-kaboom">GitHub repository</a>.
            </p>
            <MainMap />
            <p>
                Made by Peter Schmidt-Nielsen, CryZe, csunday95, and Amphitryon
                ({VERSION_STRING})
            </p>
        </>;
    }
}

export default App;
